import { Component, OnInit } from '@angular/core';
import { trigger, state, style, transition, animate} from '@angular/animations';


import { ClickService } from '../_services/click.service';

@Component({
  moduleId: module.id,
  selector: 'dashboard',
  templateUrl: 'dashboard.component.html',
  styleUrls: ['dashboard.component.css'],
  animations:[
      trigger('slideInOut', [
          state('in', style({
              width: '450px',
              left: "-450px"
          })),
          state('out', style({
              width: '450px',
              left: "0"
          })),
          transition('in => out', animate('400ms ease-in-out')),
          transition('out => in', animate('400ms ease-in-out'))
      ])
  ]
})
export class DashboardComponent implements OnInit {
  menuState: string = 'in';
  saveFileActive: boolean = false;
  saveFileAlert: boolean = false;
  confirmationActive: boolean = false;
  constructor(
    private clickService: ClickService
  ) {
        this.clickService.saveFile$.subscribe(value => {
            this.saveFileActive = value
        })
        this.clickService.sideMenu$.subscribe(value => {
            this.menuState = value;
        })
        this.clickService.alertBox$.subscribe(value => {
            this.saveFileAlert = value;
        })
        this.clickService.archiveConfirmation$.subscribe(value => {
            console.log("confirmationActive state -> ", value);
            this.confirmationActive = value;
        })
    }

  ngOnInit() {}
  toggleMenu(){
      console.log("this.menuState", this.menuState);
      this.menuState = this.menuState === 'out' ? 'in' : 'out';
      this.clickService.moveBody();
  }
}
